// check if range controller input numbers  has value
export const hasVal = (val) => val || val === 0;

// function to generate Background control's attributes
export const generateBackgroundAttributes = (controlName, defaults = {}) => {
  const {
    isBgDefaultFill,
    defaultFillColor,
    defaultBgGradient = "linear-gradient(45deg,#7967ff,#c277f2)",
  } = defaults;

  const bgColorAttr = defaultFillColor
    ? {
        [`${controlName}backgroundColor`]: {
          type: "string",
          default: defaultFillColor,
        },
      }
    : {
        [`${controlName}backgroundColor`]: {
          type: "string",
        },
      };

  return {
    // background attributes 
    [`${controlName}backgroundType`]: {
      type: "string",
      default: isBgDefaultFill === true ? "fill" : "gradient",
    },
    ...bgColorAttr,
    [`${controlName}gradientColor`]: {
      type: "string",
      default: defaultBgGradient,
    },
    [`${controlName}bgImageURL`]: {
      type: "string",
    },
    [`${controlName}bgImageID`]: {
      type: "string",
    },

    [`${controlName}backgroundSize`]: {
      type: "string",
    },
    [`${controlName}bgImgCustomSize`]: {
      type: "number",
      default: 100,
    },
    [`${controlName}bgImgCustomSizeUnit`]: {
      type: "string",
      default: "%",
    },
    [`${controlName}bgImgPos`]: {
      type: "string",
    },
    [`${controlName}bgImgcustomPosX`]: {
      type: "number",
      default: 0,
    },
    [`${controlName}bgImgcustomPosXUnit`]: {
      type: "string",
      default: "px",
    },
    [`${controlName}bgImgcustomPosY`]: {
      type: "number",
      default: 0,
    },
    [`${controlName}bgImgcustomPosYUnit`]: {
      type: "string",
      default: "px",
    },
    [`${controlName}bgImgAttachment`]: {
      type: "string",
    },
    [`${controlName}bgImgRepeat`]: {
      type: "string",
    },

    [`${controlName}isBgOverly`]: {
      type: "boolean",
      default: false,
    },
    [`${controlName}overlyType`]: {
      type: "string",
      default: "fill",
    },
    [`${controlName}overlyColor`]: {
      type: "string",
    },
    [`${controlName}overlyGradient`]: {
      type: "string",
      default: "linear-gradient(45deg,#000000cc,#00000000)",
    },

    [`TAB${controlName}backgroundSize`]: {
      type: "string",
    },
    [`TAB${controlName}bgImgCustomSize`]: {
      type: "number",
      default: 100,
    },
    [`TAB${controlName}bgImgCustomSizeUnit`]: {
      type: "string",
      default: "%",
    },
    [`TAB${controlName}bgImgPos`]: {
      type: "string",
    },
    [`TAB${controlName}bgImgcustomPosX`]: {
      type: "number",
      default: 0,
    },
    [`TAB${controlName}bgImgcustomPosXUnit`]: {
      type: "string",
      default: "px",
    },
    [`TAB${controlName}bgImgcustomPosY`]: {
      type: "number",
      default: 0,
    },
    [`TAB${controlName}bgImgcustomPosYUnit`]: {
      type: "string",
      default: "px",
    },
    [`TAB${controlName}bgImgRepeat`]: {
      type: "string",
    },

    [`MOB${controlName}backgroundSize`]: {
      type: "string",
    },
    [`MOB${controlName}bgImgCustomSize`]: {
      type: "number",
      default: 100,
    },
    [`MOB${controlName}bgImgCustomSizeUnit`]: {
      type: "string",
      default: "%",
    },
    [`MOB${controlName}bgImgPos`]: {
      type: "string",
    },
    [`MOB${controlName}bgImgcustomPosX`]: {
      type: "number",
      default: 0,
    },
    [`MOB${controlName}bgImgcustomPosXUnit`]: {
      type: "string",
      default: "px",
    },
    [`MOB${controlName}bgImgcustomPosY`]: {
      type: "number",
      default: 0,
    },
    [`MOB${controlName}bgImgcustomPosYUnit`]: {
      type: "string",
      default: "px",
    },
    [`MOB${controlName}bgImgRepeat`]: {
      type: "string",
    },
  };
};

// function to generate New Dimensions-Control's attributes for multiple Dimensions control based on the array of values(prefixs)
export const generateDimensionsAttributes = (controlName, defaults = {}) => {
  const { top, right, bottom, left } = defaults;

  const desktopTop = hasVal(top)
    ? {
        [`${controlName}Top`]: {
          type: "string",
          default: `${top}`,
        },
      }
    : {
        [`${controlName}Top`]: {
          type: "string",
        },
      };

  const desktopRight = hasVal(right)
    ? {
        [`${controlName}Right`]: {
          type: "string",
          default: `${right}`,
        },
      }
    : {
        [`${controlName}Right`]: {
          type: "string",
        },
      };

  const desktopBottom = hasVal(bottom)
    ? {
        [`${controlName}Bottom`]: {
          type: "string",
          default: `${bottom}`,
        },
      }
    : {
        [`${controlName}Bottom`]: {
          type: "string",
        },
      };

  const desktopLeft = hasVal(left)
    ? {
        [`${controlName}Left`]: {
          type: "string",
          default: `${left}`,
        },
      }
    : {
        [`${controlName}Left`]: {
          type: "string",
        },
      };

  return {
    [`${controlName}Unit`]: {
      type: "string",
      default: "px",
    },
    ...desktopTop,
    ...desktopRight,
    ...desktopBottom,
    ...desktopLeft,

    [`TAB${controlName}Unit`]: {
      type: "string",
      default: "px",
    },
    [`TAB${controlName}Top`]: {
      type: "string",
    },
    [`TAB${controlName}Right`]: {
      type: "string",
    },
    [`TAB${controlName}Bottom`]: {
      type: "string",
    },
    [`TAB${controlName}Left`]: {
      type: "string",
    },

    [`MOB${controlName}Unit`]: {
      type: "string",
      default: "px",
    },
    [`MOB${controlName}Top`]: {
      type: "string",
    },
    [`MOB${controlName}Right`]: {
      type: "string",
    },
    [`MOB${controlName}Bottom`]: {
      type: "string",
    },
    [`MOB${controlName}Left`]: {
      type: "string",
    },
  };
};

// function to generate typography attributes for multiple typography control based on the array of prefix
export const generateTypographyAttributes = (prefixArray) => {
  const typoAttrs = prefixArray.reduce((total, current) => {
    const result = {
      [`${current}FontFamily`]: {
        type: "string",
      },
      [`${current}SizeUnit`]: {
        type: "string",
        default: "px",
      },
      [`${current}FontSize`]: {
        type: "number",
      },
      [`${current}FontWeight`]: {
        type: "string",
      },
      [`${current}TextTransform`]: {
        type: "string",
      },
      [`${current}TextDecoration`]: {
        type: "string",
      },
      [`${current}LetterSpacingUnit`]: {
        type: "string",
        default: "px",
      },
      [`${current}LetterSpacing`]: {
        type: "number",
      },
      [`${current}LineHeightUnit`]: {
        type: "string",
        default: "em",
      },
      [`${current}LineHeight`]: {
        type: "number",
      },

      [`TAB${current}SizeUnit`]: {
        type: "string",
        default: "px",
      },
      [`TAB${current}FontSize`]: {
        type: "number",
      },
      [`TAB${current}LetterSpacingUnit`]: {
        type: "string",
        default: "px",
      },
      [`TAB${current}LetterSpacing`]: {
        type: "number",
      },
      [`TAB${current}LineHeightUnit`]: {
        type: "string",
        default: "em",
      },
      [`TAB${current}LineHeight`]: {
        type: "number",
      },

      [`MOB${current}SizeUnit`]: {
        type: "string",
        default: "px",
      },
      [`MOB${current}FontSize`]: {
        type: "number",
      },
      [`MOB${current}LetterSpacingUnit`]: {
        type: "string",
        default: "px",
      },
      [`MOB${current}LetterSpacing`]: {
        type: "number",
      },
      [`MOB${current}LineHeightUnit`]: {
        type: "string",
        default: "em",
      },
      [`MOB${current}LineHeight`]: {
        type: "number",
      },
    };
    return {
      ...total,
      ...result,
    };
  }, {});

  // console.log({ typoAttrs });
  return typoAttrs;
};

// Important: the following "generateBorderShadowAttributes" function must be declared below the "generateDimensionsAttributes" function declaration
// function to generate BorderShadow control's attributes
export const generateBorderShadowAttributes = (controlName, defaults = {}) => {
  const { bdrDefaults = {}, rdsDefaults = {} } = defaults;

  const result = {
    // Border Shadow attributes 

    // border attributes 
    [`${controlName}BorderType`]: {
      type: "string",
      default: "normal",
    },
    [`${controlName}borderColor`]: {
      type: "string",
    },
    [`${controlName}borderStyle`]: {
      type: "string",
      default: "none",
    },
    [`${controlName}HborderColor`]: {
      type: "string",
    },
    [`${controlName}HborderStyle`]: {
      type: "string",
      default: "none",
    },

    // shadow attributes  
    [`${controlName}hOffset`]: {
      type: "number",
    },
    [`${controlName}vOffset`]: {
      type: "number",
    },
    [`${controlName}blur`]: {
      type: "number",
    },
    [`${controlName}spread`]: {
      type: "number",
    },
    [`${controlName}shadowColor`]: {
      type: "string",
    },
    [`${controlName}inset`]: {
      type: "boolean",
      default: false,
    },
    [`${controlName}shadowType`]: {
      type: "string",
      default: "normal",
    },
    [`${controlName}hoverHOffset`]: {
      type: "number",
    },
    [`${controlName}hoverVOffset`]: {
      type: "number",
    },
    [`${controlName}hoverBlur`]: {
      type: "number",
    },
    [`${controlName}hoverSpread`]: {
      type: "number",
    },
    [`${controlName}hoverShadowColor`]: {
      type: "string",
    },
    [`${controlName}hoverInset`]: {
      type: "boolean",
      default: false,
    },
    [`${controlName}transitionTime`]: {
      type: "string",
      default: "500",
    },

    ...generateDimensionsAttributes(`${controlName}Bdr_`, bdrDefaults),
    ...generateDimensionsAttributes(`${controlName}Rds_`, rdsDefaults),
    ...generateDimensionsAttributes(`${controlName}HBdr_`),
    ...generateDimensionsAttributes(`${controlName}HRds_`),
  };

  // console.log("---generateBorderShadowAttributes:", { result });

  return result;
};

// helper Functions: function 'textInsideForEdit' is for setting the innertext depending on whether separator should be shown and which separator should be shown
export const textInsideForEdit = (value, isShowSeparator, separator) =>
  isShowSeparator
    ? value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, separator)
    : value.toString();

// generateRandomNumber function is for generating a random number
export const generateRandomNumber = () =>
  Math.floor(Math.random() * 1000000000);

// hardMinifyCssStrings is for minifying the css which is in the style tag as a string  for view.js
export const hardMinifyCssStrings = (cssString) => {
  // console.log({ cssString });

  return (
    cssString
      .replace(/\s+/g, " ")
      .replace(/(?<=\:).+(?=\;)/g, function (match) {
        // console.log({ match, g1, offset, string });
        return match.trim().replace(/\s+/g, "__s_p_a_c_e__");
      })
      // .replace(/\s+(?!(?:[\w\d\.\-\#]+\{))/g, "")
      .replace(/\s+(?![\w\d\.\-\#]+\{)/g, "")
      .replace(/\s+/g, " ")
      .replace(/__s_p_a_c_e__/g, " ")
  );
};

// softMinifyCssStrings is for minifying the css which is in the style tag as a string  for view.js
export const softMinifyCssStrings = (cssString) =>
  cssString.replace(/\s+/g, " ");

// check if css string is empty or not.
export const isCssExists = (cssString) =>
  /.+(?=\:(?!hover)(?!focus))/.test(cssString);

//
// function to generate typography styles for an element based on it's prefix
export const generateTypographyStyles = ({
  prefixConstant,
  defaultFontSize,
  attributes,
}) => {
  const {
    [`${prefixConstant}FontFamily`]: fontFamily,
    [`${prefixConstant}FontWeight`]: fontWeight,
    [`${prefixConstant}TextTransform`]: textTransform,
    [`${prefixConstant}TextDecoration`]: textDecoration,
    [`${prefixConstant}FontSize`]: fontSize = defaultFontSize,
    [`${prefixConstant}SizeUnit`]: sizeUnit,
    [`${prefixConstant}LetterSpacing`]: letterSpacing,
    [`${prefixConstant}LetterSpacingUnit`]: letterSpacingUnit,
    [`${prefixConstant}LineHeight`]: lineHeight,
    [`${prefixConstant}LineHeightUnit`]: lineHeightUnit,

    [`TAB${prefixConstant}SizeUnit`]: TABsizeUnit,
    [`TAB${prefixConstant}LetterSpacingUnit`]: TABletterSpacingUnit,
    [`TAB${prefixConstant}LineHeightUnit`]: TABlineHeightUnit,
    [`TAB${prefixConstant}FontSize`]: TABfontSize,
    [`TAB${prefixConstant}LetterSpacing`]: TABletterSpacing,
    [`TAB${prefixConstant}LineHeight`]: TABlineHeight,

    [`MOB${prefixConstant}SizeUnit`]: MOBsizeUnit,
    [`MOB${prefixConstant}LetterSpacingUnit`]: MOBletterSpacingUnit,
    [`MOB${prefixConstant}LineHeightUnit`]: MOBlineHeightUnit,
    [`MOB${prefixConstant}FontSize`]: MOBfontSize,
    [`MOB${prefixConstant}LetterSpacing`]: MOBletterSpacing,
    [`MOB${prefixConstant}LineHeight`]: MOBlineHeight,
  } = attributes;

  const typoStylesDesktop = `
			${fontFamily ? `font-family: ${fontFamily};` : " "}
			${hasVal(fontSize) ? `font-size: ${fontSize}${sizeUnit};` : " "}
			${hasVal(lineHeight) ? `line-height: ${lineHeight}${lineHeightUnit};` : " "}
			${fontWeight ? `font-weight: ${fontWeight};` : " "}
			${textDecoration ? `text-decoration: ${textDecoration};` : " "}
			${textTransform ? `text-transform: ${textTransform};` : " "}
			${
        hasVal(letterSpacing)
          ? `letter-spacing: ${letterSpacing}${letterSpacingUnit};`
          : " "
      }
		`;

  const typoStylesTab = `
			${hasVal(TABfontSize) ? `font-size: ${TABfontSize}${TABsizeUnit};` : " "}
			${
        hasVal(TABlineHeight)
          ? `line-height: ${TABlineHeight}${TABlineHeightUnit};`
          : " "
      }
			${
        hasVal(TABletterSpacing)
          ? `letter-spacing: ${TABletterSpacing}${TABletterSpacingUnit};`
          : " "
      }
		`;

  const typoStylesMobile = `
			${hasVal(MOBfontSize) ? `font-size: ${MOBfontSize}${MOBsizeUnit};` : " "}
			${
        hasVal(MOBlineHeight)
          ? `line-height: ${MOBlineHeight}${MOBlineHeightUnit};`
          : " "
      }
			${
        hasVal(MOBletterSpacing)
          ? `letter-spacing: ${MOBletterSpacing}${MOBletterSpacingUnit};`
          : " "
      }
		`;

  return {
    typoStylesDesktop,
    typoStylesTab,
    typoStylesMobile,
  };
};

//
// function to generate dimensions-controls styles for an element based on it's controlName(prefix)
export const generateDimensionsControlStyles = ({
  controlName,
  styleFor,
  attributes,
}) => {
  const {
    [`${controlName}Unit`]: dimensionUnit,
    [`${controlName}Top`]: dimensionTop,
    [`${controlName}Right`]: dimensionRight,
    [`${controlName}Bottom`]: dimensionBottom,
    [`${controlName}Left`]: dimensionLeft,

    [`TAB${controlName}Unit`]: TABdimensionUnit,
    [`TAB${controlName}Top`]: TABdimensionTop,
    [`TAB${controlName}Right`]: TABdimensionRight,
    [`TAB${controlName}Bottom`]: TABdimensionBottom,
    [`TAB${controlName}Left`]: TABdimensionLeft,

    [`MOB${controlName}Unit`]: MOBdimensionUnit,
    [`MOB${controlName}Top`]: MOBdimensionTop,
    [`MOB${controlName}Right`]: MOBdimensionRight,
    [`MOB${controlName}Bottom`]: MOBdimensionBottom,
    [`MOB${controlName}Left`]: MOBdimensionLeft,
  } = attributes;

  // console.log({ controlName, attributes });

  let dimensionStylesDesktop = " ";
  let dimensionStylesTab = " ";
  let dimensionStylesMobile = " ";

  if (styleFor === "border") {
    dimensionStylesDesktop = `
		${
      dimensionTop
        ? `border-top-width: ${parseFloat(dimensionTop)}${dimensionUnit};`
        : " "
    }
		${
      dimensionRight
        ? `border-right-width: ${parseFloat(dimensionRight)}${dimensionUnit};`
        : " "
    }
		${
      dimensionLeft
        ? `border-left-width: ${parseFloat(dimensionLeft)}${dimensionUnit};`
        : " "
    }
		${
      dimensionBottom
        ? `border-bottom-width: ${parseFloat(dimensionBottom)}${dimensionUnit};`
        : " "
    }
	
		`;

    dimensionStylesTab = `
			${
        TABdimensionTop
          ? `border-top-width: ${parseFloat(
              TABdimensionTop
            )}${TABdimensionUnit};`
          : " "
      }
			${
        TABdimensionRight
          ? `border-right-width: ${parseFloat(
              TABdimensionRight
            )}${TABdimensionUnit};`
          : " "
      }
			${
        TABdimensionLeft
          ? `border-left-width: ${parseFloat(
              TABdimensionLeft
            )}${TABdimensionUnit};`
          : " "
      }
			${
        TABdimensionBottom
          ? `border-bottom-width: ${parseFloat(
              TABdimensionBottom
            )}${TABdimensionUnit};`
          : " "
      }

		`;

    dimensionStylesMobile = `
			${
        MOBdimensionTop
          ? `border-top-width: ${parseFloat(
              MOBdimensionTop
            )}${MOBdimensionUnit};`
          : " "
      }
			${
        MOBdimensionRight
          ? `border-right-width: ${parseFloat(
              MOBdimensionRight
            )}${MOBdimensionUnit};`
          : " "
      }
			${
        MOBdimensionLeft
          ? `border-left-width: ${parseFloat(
              MOBdimensionLeft
            )}${MOBdimensionUnit};`
          : " "
      }
			${
        MOBdimensionBottom
          ? `border-bottom-width: ${parseFloat(
              MOBdimensionBottom
            )}${MOBdimensionUnit};`
          : " "
      }

		`;
  } else if (styleFor === "border-radius") {
    dimensionStylesDesktop = `
			${
        dimensionTop
          ? `border-top-left-radius: ${parseFloat(
              dimensionTop
            )}${dimensionUnit};`
          : " "
      }
			${
        dimensionRight
          ? `border-top-right-radius: ${parseFloat(
              dimensionRight
            )}${dimensionUnit};`
          : " "
      }
			${
        dimensionLeft
          ? `border-bottom-left-radius: ${parseFloat(
              dimensionLeft
            )}${dimensionUnit};`
          : " "
      }
			${
        dimensionBottom
          ? `border-bottom-right-radius: ${parseFloat(
              dimensionBottom
            )}${dimensionUnit};`
          : " "
      }
	
		`;

    dimensionStylesTab = `
			${
        TABdimensionTop
          ? `border-top-left-radius: ${parseFloat(
              TABdimensionTop
            )}${TABdimensionUnit};`
          : " "
      }
			${
        TABdimensionRight
          ? `border-top-right-radius: ${parseFloat(
              TABdimensionRight
            )}${TABdimensionUnit};`
          : " "
      }
			${
        TABdimensionLeft
          ? `border-bottom-left-radius: ${parseFloat(
              TABdimensionLeft
            )}${TABdimensionUnit};`
          : " "
      }
			${
        TABdimensionBottom
          ? `border-bottom-right-radius: ${parseFloat(
              TABdimensionBottom
            )}${TABdimensionUnit};`
          : " "
      }

		`;

    dimensionStylesMobile = `
			${
        MOBdimensionTop
          ? `border-top-left-radius: ${parseFloat(
              MOBdimensionTop
            )}${MOBdimensionUnit};`
          : " "
      }
			${
        MOBdimensionRight
          ? `border-top-right-radius: ${parseFloat(
              MOBdimensionRight
            )}${MOBdimensionUnit};`
          : " "
      }
			${
        MOBdimensionLeft
          ? `border-bottom-left-radius: ${parseFloat(
              MOBdimensionLeft
            )}${MOBdimensionUnit};`
          : " "
      }
			${
        MOBdimensionBottom
          ? `border-bottom-right-radius: ${parseFloat(
              MOBdimensionBottom
            )}${MOBdimensionUnit};`
          : " "
      }

		`;
  } else {
    dimensionStylesDesktop = `
		${
      dimensionTop
        ? `${styleFor}-top: ${parseFloat(dimensionTop)}${dimensionUnit};`
        : " "
    }
		${
      dimensionRight
        ? `${styleFor}-right: ${parseFloat(dimensionRight)}${dimensionUnit};`
        : " "
    }
		${
      dimensionLeft
        ? `${styleFor}-left: ${parseFloat(dimensionLeft)}${dimensionUnit};`
        : " "
    }
		${
      dimensionBottom
        ? `${styleFor}-bottom: ${parseFloat(dimensionBottom)}${dimensionUnit};`
        : " "
    }
	
		`;

    dimensionStylesTab = `
			${
        TABdimensionTop
          ? `${styleFor}-top: ${parseFloat(
              TABdimensionTop
            )}${TABdimensionUnit};`
          : " "
      }
			${
        TABdimensionRight
          ? `${styleFor}-right: ${parseFloat(
              TABdimensionRight
            )}${TABdimensionUnit};`
          : " "
      }
			${
        TABdimensionLeft
          ? `${styleFor}-left: ${parseFloat(
              TABdimensionLeft
            )}${TABdimensionUnit};`
          : " "
      }
			${
        TABdimensionBottom
          ? `${styleFor}-bottom: ${parseFloat(
              TABdimensionBottom
            )}${TABdimensionUnit};`
          : " "
      }

		`;

    dimensionStylesMobile = `
			${
        MOBdimensionTop
          ? `${styleFor}-top: ${parseFloat(
              MOBdimensionTop
            )}${MOBdimensionUnit};`
          : " "
      }
			${
        MOBdimensionRight
          ? `${styleFor}-right: ${parseFloat(
              MOBdimensionRight
            )}${MOBdimensionUnit};`
          : " "
      }
			${
        MOBdimensionLeft
          ? `${styleFor}-left: ${parseFloat(
              MOBdimensionLeft
            )}${MOBdimensionUnit};`
          : " "
      }
			${
        MOBdimensionBottom
          ? `${styleFor}-bottom: ${parseFloat(
              MOBdimensionBottom
            )}${MOBdimensionUnit};`
          : " "
      }

		`;
  }

  return {
    dimensionStylesDesktop,
    dimensionStylesTab,
    dimensionStylesMobile,
  };
};

// Important: the following "generateBorderShadowStyles" function must be declared below the "generateDimensionsControlStyles" function declaration
// function to generate BorderShadow control's Styles for an element based on it's controlName(prefix)
export const generateBorderShadowStyles = ({ controlName, attributes }) => {
  const {
    dimensionStylesDesktop: borderStylesDesktop,
    dimensionStylesTab: borderStylesTab,
    dimensionStylesMobile: borderStylesMobile,
  } = generateDimensionsControlStyles({
    controlName: `${controlName}Bdr_`,
    styleFor: "border",
    attributes,
  });

  const {
    dimensionStylesDesktop: radiusStylesDesktop,
    dimensionStylesTab: radiusStylesTab,
    dimensionStylesMobile: radiusStylesMobile,
  } = generateDimensionsControlStyles({
    controlName: `${controlName}Rds_`,
    styleFor: "border-radius",
    attributes,
  });

  const {
    dimensionStylesDesktop: HborderStylesDesktop,
    dimensionStylesTab: HborderStylesTab,
    dimensionStylesMobile: HborderStylesMobile,
  } = generateDimensionsControlStyles({
    controlName: `${controlName}HBdr_`,
    styleFor: "border",
    attributes,
  });

  const {
    dimensionStylesDesktop: HradiusStylesDesktop,
    dimensionStylesTab: HradiusStylesTab,
    dimensionStylesMobile: HradiusStylesMobile,
  } = generateDimensionsControlStyles({
    controlName: `${controlName}HRds_`,
    styleFor: "border-radius",
    attributes,
  });

  const {
    // [`${controlName}BorderType`]: BorderType,
    [`${controlName}borderStyle`]: borderStyle,
    [`${controlName}borderColor`]: borderColor,
    [`${controlName}HborderStyle`]: HborderStyle,
    [`${controlName}HborderColor`]: HborderColor = borderColor,

    [`${controlName}shadowColor`]: shadowColor,
    [`${controlName}hOffset`]: hOffset = 0,
    [`${controlName}vOffset`]: vOffset = 0,
    [`${controlName}blur`]: blur = 0,
    [`${controlName}spread`]: spread = 0,
    [`${controlName}inset`]: inset,

    [`${controlName}hoverShadowColor`]: hoverShadowColor = shadowColor,
    [`${controlName}hoverHOffset`]: hoverHOffset = hOffset,
    [`${controlName}hoverVOffset`]: hoverVOffset = vOffset,
    [`${controlName}hoverBlur`]: hoverBlur = blur,
    [`${controlName}hoverSpread`]: hoverSpread = spread,

    [`${controlName}transitionTime`]: transitionTime,
  } = attributes;

  const styesDesktop = `
    ${borderStylesDesktop}
    ${radiusStylesDesktop}


    ${
      borderStyle !== "none" && borderColor
        ? `
        ${borderStylesDesktop}
        border-color: ${borderColor};
        border-style: ${borderStyle};
        `
        : " "
    }

    ${
      shadowColor
        ? `box-shadow: ${shadowColor} ${hOffset}px ${vOffset}px ${blur}px ${spread}px ${
            inset ? "inset" : ""
          };`
        : " "
    }

    transition: ${transitionTime ? `${transitionTime / 1000}s` : ".5s"};

  `;

  const styesTab = `  
    ${borderColor ? borderStylesTab : " "}
    ${radiusStylesTab}
  `;

  const styesMobile = `
    ${borderColor ? borderStylesMobile : " "}
    ${radiusStylesMobile}
  `;

  const stylesHoverDesktop = `
    ${
      hoverShadowColor
        ? `box-shadow: ${hoverShadowColor} ${hoverHOffset}px ${hoverVOffset}px ${hoverBlur}px ${hoverSpread}px ${
            inset ? "inset" : " "
          };`
        : " "
    }

    ${
      HborderStyle !== "none"
        ? `
          ${
            HborderColor !== borderColor
              ? `border-color: ${HborderColor};`
              : " "
          } 
          ${
            HborderStyle !== borderStyle
              ? `border-style: ${HborderStyle};`
              : " "
          }
          ${HborderStylesDesktop}
        `
        : " "
    }

    ${HradiusStylesDesktop}    

  `;

  const stylesHoverTab = `
    ${HborderStyle !== "none" ? HborderStylesTab : " "}
    ${HradiusStylesTab}  
  `;

  const stylesHoverMobile = `
    ${HborderStyle !== "none" ? HborderStylesMobile : " "}
    ${HradiusStylesMobile}  
  `;

  return {
    styesDesktop,
    styesTab,
    styesMobile,
    stylesHoverDesktop,
    stylesHoverTab,
    stylesHoverMobile,
  };
};

// function to generate Background control styles based on the unique controlName(prefix)
export const generateBackgroundControlStyles = ({
  controlName,
  attributes,
}) => {
  const {
    // background attributes 
    [`${controlName}backgroundType`]: backgroundType,
    [`${controlName}backgroundColor`]: backgroundColor,
    [`${controlName}gradientColor`]: gradientColor,
    [`${controlName}bgImageURL`]: bgImageURL,
    // [`${controlName}bgImageID`]: bgImageID,
    [`${controlName}backgroundSize`]: backgroundSize,
    [`${controlName}bgImgCustomSize`]: bgImgCustomSize,
    [`${controlName}bgImgCustomSizeUnit`]: bgImgCustomSizeUnit,
    [`${controlName}bgImgPos`]: bgImgPos,
    [`${controlName}bgImgcustomPosX`]: bgImgcustomPosX,
    [`${controlName}bgImgcustomPosXUnit`]: bgImgcustomPosXUnit,
    [`${controlName}bgImgcustomPosY`]: bgImgcustomPosY,
    [`${controlName}bgImgcustomPosYUnit`]: bgImgcustomPosYUnit,
    [`${controlName}bgImgAttachment`]: bgImgAttachment,
    [`${controlName}bgImgRepeat`]: bgImgRepeat,

    [`${controlName}overlyColor`]: overlyColor = "#00000080",
    [`${controlName}overlyType`]: overlyType,
    [`${controlName}isBgOverly`]: isBgOverly,
    [`${controlName}overlyGradient`]: overlyGradient,

    [`TAB${controlName}backgroundSize`]: TABbackgroundSize,
    [`TAB${controlName}bgImgCustomSize`]: TABbgImgCustomSize,
    [`TAB${controlName}bgImgCustomSizeUnit`]: TABbgImgCustomSizeUnit,
    [`TAB${controlName}bgImgPos`]: TABbgImgPos,
    [`TAB${controlName}bgImgcustomPosX`]: TABbgImgcustomPosX,
    [`TAB${controlName}bgImgcustomPosXUnit`]: TABbgImgcustomPosXUnit,
    [`TAB${controlName}bgImgcustomPosY`]: TABbgImgcustomPosY,
    [`TAB${controlName}bgImgcustomPosYUnit`]: TABbgImgcustomPosYUnit,
    [`TAB${controlName}bgImgRepeat`]: TABbgImgRepeat,

    [`MOB${controlName}backgroundSize`]: MOBbackgroundSize,
    [`MOB${controlName}bgImgCustomSize`]: MOBbgImgCustomSize,
    [`MOB${controlName}bgImgCustomSizeUnit`]: MOBbgImgCustomSizeUnit,
    [`MOB${controlName}bgImgPos`]: MOBbgImgPos,
    [`MOB${controlName}bgImgcustomPosX`]: MOBbgImgcustomPosX,
    [`MOB${controlName}bgImgcustomPosXUnit`]: MOBbgImgcustomPosXUnit,
    [`MOB${controlName}bgImgcustomPosY`]: MOBbgImgcustomPosY,
    [`MOB${controlName}bgImgcustomPosYUnit`]: MOBbgImgcustomPosYUnit,
    [`MOB${controlName}bgImgRepeat`]: MOBbgImgRepeat,
  } = attributes;

  const backgroundStylesDesktop = `
    background-image: ${
      backgroundType === "image" && bgImageURL
        ? `url("${bgImageURL}")`
        : backgroundType === "gradient"
        ? gradientColor
        : "none"
    };

    ${
      backgroundType === "image" && bgImageURL
        ? `
        ${
          backgroundSize && backgroundSize !== "custom"
            ? `background-size: ${backgroundSize};`
            : backgroundSize === "custom"
            ? `background-size: ${bgImgCustomSize}${bgImgCustomSizeUnit} auto;`
            : " "
        }

        ${
          bgImgPos && bgImgPos !== "custom"
            ? `background-position: ${bgImgPos};`
            : bgImgPos === "custom"
            ? `background-position: ${bgImgcustomPosX}${bgImgcustomPosXUnit} ${bgImgcustomPosY}${bgImgcustomPosYUnit};`
            : " "
        }

        ${bgImgAttachment ? `background-attachment: ${bgImgAttachment};` : " "}

        ${bgImgRepeat ? `background-repeat: ${bgImgRepeat};` : " "}

        `
        : " "
    }
  
		${backgroundColor ? `background-color: ${backgroundColor};` : " "}
    ${
      isBgOverly
        ? `
          z-index: 2;
          position: relative;
        `
        : " "
    }	

  `;

  const backgroundStylesTab = `
    ${
      backgroundType === "image" && bgImageURL
        ? `
        ${
          TABbackgroundSize && TABbackgroundSize !== "custom"
            ? `background-size: ${TABbackgroundSize};`
            : TABbackgroundSize === "custom"
            ? `background-size: ${TABbgImgCustomSize}${TABbgImgCustomSizeUnit} auto;`
            : " "
        }

        ${
          TABbgImgPos && TABbgImgPos !== "custom"
            ? `background-position: ${TABbgImgPos};`
            : TABbgImgPos === "custom"
            ? `background-position: ${TABbgImgcustomPosX}${TABbgImgcustomPosXUnit} ${TABbgImgcustomPosY}${TABbgImgcustomPosYUnit};`
            : " "
        }

        ${TABbgImgRepeat ? `background-repeat: ${TABbgImgRepeat};` : " "}

        `
        : " "
    }

    ${backgroundType === "image" ? `background-attachment: scroll;` : " "}

  `;

  const backgroundStylesMobile = `
    ${
      backgroundType === "image" && bgImageURL
        ? `
        ${
          MOBbackgroundSize && MOBbackgroundSize !== "custom"
            ? `background-size: ${MOBbackgroundSize};`
            : MOBbackgroundSize === "custom"
            ? `background-size: ${MOBbgImgCustomSize}${MOBbgImgCustomSizeUnit} auto;`
            : " "
        }

        ${
          MOBbgImgPos && MOBbgImgPos !== "custom"
            ? `background-position: ${MOBbgImgPos};`
            : MOBbgImgPos === "custom"
            ? `background-position: ${MOBbgImgcustomPosX}${MOBbgImgcustomPosXUnit} ${MOBbgImgcustomPosY}${MOBbgImgcustomPosYUnit};`
            : " "
        }

        ${MOBbgImgRepeat ? `background-repeat: ${MOBbgImgRepeat};` : " "}

        `
        : " "
    }

  `;

  const overlyStyles = `
  
    ${
      backgroundType === "image" && isBgOverly
        ? `
          content: "";
          position: absolute;
          top: 0;
          bottom: 0;
          right: 0;
          left: 0;
          z-index: -1;

          ${
            overlyType === "fill"
              ? `background-color: ${overlyColor};`
              : overlyType === "gradient"
              ? `background-image: ${overlyGradient};`
              : " "
          }
      `
        : " "
    }
  
  
  `;

  return {
    backgroundStylesDesktop,
    backgroundStylesTab,
    backgroundStylesMobile,
    overlyStyles,
  };
};

// function to generate responsive range controller attributes for multiple range control based on the array of prefix
export const generateResponsiveRangeAttributes = (
  controlName,
  defaultRange = false
) => {
  const desktop = defaultRange
    ? {
        [`${controlName}Range`]: {
          type: "number",
          default: `${defaultRange}`,
        },
      }
    : {
        [`${controlName}Range`]: {
          type: "number",
        },
      };

  const result = {
    [`${controlName}Unit`]: {
      type: "string",
      default: "px",
    },
    ...desktop,
    [`TAB${controlName}Unit`]: {
      type: "string",
      default: "px",
    },
    [`TAB${controlName}Range`]: {
      type: "number",
    },
    [`MOB${controlName}Unit`]: {
      type: "string",
      default: "px",
    },
    [`MOB${controlName}Range`]: {
      type: "number",
    },
  };

  return { ...result };
};

// function to generate responsive range control styles for an element based on it's prefix
export const generateResponsiveRangeStyles = ({
  controlName,
  property,
  attributes,
}) => {
  const {
    [`${controlName}Unit`]: desktopSizeUnit,
    [`${controlName}Range`]: desktopRange,

    [`TAB${controlName}Unit`]: TABsizeUnit,
    [`TAB${controlName}Range`]: TABrange,

    [`MOB${controlName}Unit`]: MOBsizeUnit,
    [`MOB${controlName}Range`]: MOBrange,
  } = attributes;

  const size = isNaN(parseFloat(desktopRange)) ? "" : desktopSizeUnit;
  const rangeStylesDesktop = desktopRange
    ? property + ":" + desktopRange + size + ";"
    : "";
  const rangeStylesTab = TABrange
    ? property + ":" + TABrange + TABsizeUnit + ";"
    : "";
  const rangeStylesMobile = MOBrange
    ? property + ":" + MOBrange + MOBsizeUnit + ";"
    : "";

  return {
    rangeStylesDesktop,
    rangeStylesTab,
    rangeStylesMobile,
  };
};

// Return flilp value based on type
export const getFlipTransform = (flipType) => {
  switch (flipType) {
    case "flip-left":
      return "rotateY(-180deg)";

    case "flip-right":
      return "rotateY(180deg)";

    case "flip-up":
      return "rotateX(180deg)";

    case "flip-bottom":
      return "rotateX(-180deg)";

    case "zoom-in":
      return "scale(1.1)";

    case "zoom-out":
      return "scale(0.8)";
  }
};

// Return css class names based on button style name
export const getButtonClasses = (buttonStyle) => {
  switch (buttonStyle) {
    case "styleOne":
      return "btn-gradient blue";

    case "styleTwo":
      return "btn-gradient purple";

    case "styleThree":
      return "btn-gradient orange";

    case "custom":
      return "";
  }
};

export const getBackgroundImage = (type, gradientValue, imageURL) => {
  switch (type) {
    case "fill":
      return "none";

    case "gradient":
      return gradientValue;

    case "image":
      if (imageURL) {
        return `url(${imageURL})`;
      }
      return "none";
  }
};
